import { yupResolver } from '@hookform/resolvers/yup'
import SaveIcon from '@mui/icons-material/Save'
import { LoadingButton, TabContext, TabList, TabPanel } from '@mui/lab'
import {
	Autocomplete,
	Box,
	FormControl,
	InputLabel,
	MenuItem,
	Select,
	Stack,
	Tab,
	TextField,
	Typography,
} from '@mui/material'
import { PageApi } from 'API/CMS/page'
import { useEffect, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getPages } from 'redux/slices/CMS/Page'
import { getTemplates } from 'redux/slices/CMS/Template'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'
interface FormInput {
	webId: number
	themeId: number
	url: string
	title: string
	description?: string
	body?: { [key: string]: string }
	style?: string
	script?: string
}
export default function Create() {
	const { webId } = useParams<{ webId: string }>()
	const dispatch = useAppDispatch()
	const nav = useNavigate()
	const [loading, setLoading] = useState(false)

	const status = useAppSelector((s) => s.CMS.Page.status)
	useEffect(() => {
		if (status === 'init') dispatch(getPages())
	}, [status, dispatch])
	const templateStatus = useAppSelector((s) => s.CMS.Template.status)
	useEffect(() => {
		if (templateStatus === 'init') dispatch(getTemplates())
	}, [dispatch, templateStatus])

	const [createdId, setCreatedId] = useState(0)
	const [tab, setTab] = useState('template')

	const pages = useAppSelector((s) => s.CMS.Page.list).filter((f) => webId && f.webId === parseInt(webId) && f.active)
	const templates = useAppSelector((s) => s.CMS.Template.list).filter((f) => f.active)

	useEffect(() => {
		if (pages.find((f) => f.id === createdId)) {
			nav(`/cms/${webId}/page/${createdId}`)
		}
	}, [createdId, pages, nav, webId])
	const schema = yup.object({
		url: yup
			.string()
			.required()
			.matches(/^\/[a-z0-9-_/.]+html$(?!\/)/gm),
		title: yup.string().required(),
		webId: yup.number().required().min(1),
		themeId: yup.number().required().min(1),
	})
	const {
		register,
		handleSubmit,
		setValue,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			url: '',
			webId: webId ? parseInt(webId) : 0,
			themeId: 0,
			title: '',
			body: {},
			description: '',
			style: '',
			script: '',
		},
	})
	const onSubmit: SubmitHandler<FormInput> = async (body) => {
		try {
			setLoading(true)
			const { id, message, error } = await PageApi.create(body)
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message,
				})
			)
			id && setCreatedId(id)
		} finally {
			setLoading(false)
		}
	}
	return (
		<Stack gap={1} component="form" noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
			<Stack direction="row" alignItems="center" justifyContent="space-between">
				<Typography
					color="primary.dark"
					sx={{
						fontWeight: 600,
					}}
				>
					ウェブページ作成
				</Typography>
			</Stack>
			<Stack gap={2} maxWidth="sm" my={2}>
				<TabContext value={tab}>
					<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
						<TabList
							value={tab}
							onChange={(_, val) => {
								setValue('themeId', 0)
								setTab(`${val}`)
							}}
						>
							<Tab label="テンプレート選択" value="template" />
							<Tab label="ページ複製" value="page" />
						</TabList>
					</Box>
					<TabPanel
						value="template"
						sx={{
							p: 0,
						}}
					>
						<FormControl fullWidth size="small">
							<InputLabel id="ページ複製">テンプレート選択</InputLabel>
							<Select
								size="small"
								labelId="テンプレート選択"
								label="テンプレート選択"
								defaultValue={0}
								error={'themeId' in errors}
								onChange={(e) => {
									const { value } = e.target
									const copied = templates.find((f) => f.id === value)
									if (copied) {
										setValue('themeId', copied.themeId, { shouldValidate: true })
										setValue('body', copied.body)
										setValue('script', copied.script)
										setValue('style', copied.style)
									}
								}}
							>
								<MenuItem disabled value={0}>
									<Typography component="em" variant="caption">
										選択してください
									</Typography>
								</MenuItem>
								{templates.map((v) => {
									return (
										<MenuItem key={v.id} value={v.id}>
											{v.name}
										</MenuItem>
									)
								})}
							</Select>
						</FormControl>
					</TabPanel>
					<TabPanel
						value="page"
						sx={{
							p: 0,
						}}
					>
						<Autocomplete
							disablePortal
							options={[0, ...pages.map((v) => v.id)]}
							fullWidth
							getOptionLabel={(t) => {
								let label = ''
								if (t === 0) label = ''
								else label = pages.find((f) => f.id === t)?.title || ''
								return label
							}}
							getOptionDisabled={(t) => t === 0}
							defaultValue={0}
							renderInput={(params) => (
								<TextField
									{...params}
									label={'ページ複製'}
									placeholder="ページ名を入力すると項目の絞込みができます"
									InputLabelProps={{ shrink: true }}
									variant="outlined"
									size="small"
									error={'themeId' in errors}
								/>
							)}
							onChange={(_event, t) => {
								if (t) {
									const copied = pages.find((f) => f.id === t)
									if (copied) {
										setValue('themeId', copied.themeId, { shouldValidate: true })
										setValue('body', copied.body)
										setValue('script', copied.script)
										setValue('style', copied.style)
									}
								}
							}}
						/>
					</TabPanel>
				</TabContext>
				<TextField
					variant="outlined"
					size="small"
					margin="dense"
					autoFocus
					required
					label={'ページURL'}
					placeholder="例：/index.html, /about.html, /news/2022-01-01.html"
					{...register('url')}
					error={'url' in errors}
				/>

				<TextField
					variant="outlined"
					size="small"
					margin="dense"
					required
					label={'タイトル'}
					placeholder="例：ホーム"
					{...register('title')}
					error={'title' in errors}
				/>
				<TextField
					variant="outlined"
					size="small"
					multiline
					minRows={3}
					margin="dense"
					label={'ページ説明'}
					placeholder="入力"
					{...register('description')}
					error={'description' in errors}
				/>
			</Stack>
			<Box>
				<LoadingButton
					variant="outlined"
					disabled={Object.keys(errors).length > 0}
					loading={loading}
					loadingPosition="start"
					startIcon={<SaveIcon />}
					type="submit"
				>
					作成する
				</LoadingButton>
			</Box>
		</Stack>
	)
}
